/* mobile */


.app {
    width: 100vw;
    height: 100vh
  }
  
  body {
    margin: 0;
    padding: 0;
    background-color: #000000;
  }
  
  
  .icon {
    font-size: 20px;
    margin: auto 5px;
    cursor: pointer;
    transition: 0.3s;
    margin-bottom: 0px;
    color: #d5d4b7
  }
  
  .icon:hover {
    color: rgb(231, 203, 21);
  }
  
  .home-separator-line {
    border: 0; /* Remove default border */
    height: 1px; /* Height of the line */
    background-color:#d5d4b7; /* Line color */
    margin-right: 0px;
  }
  
  .home-content-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    max-width: 500px;
    margin: 0 auto; /* Centers the container horizontally */
   
  }
  
  .home-text-content {
    background-color: #221a20;
    height: 135px;
    margin-top: 0px
  }
  
  .home-text-content {
    flex: 1; /* Adjust as needed */
    padding: 20px; /* Optional padding */
  }
  
  .home-image-content {
    margin-top: 25px;
    flex: 1; /* Adjust as needed */
    display: flex; /* Use flexbox */
    justify-content: flex-end; /* Align children to the right */
  }
  
  .home-title {
    font-family: 'Reem Kufi Fun', sans-serif;
    text-align: center;
    font-size: 10px;
    color: rgb(245,195,40);
    margin-bottom: 0px;
    margin-top: 0px;
    margin-right: 0px;
  }
  
  .home-text {
    margin-top: 0px;
    margin-bottom: 40px;
    font-family: 'Reem Kufi Fun', sans-serif;
    text-align: center;
    font-size: 7px;
    color:#d5d4b7;
    margin-right: 0px;
  }
  
  .home-image {
    width: 100%;
    height: 175px;
    width: 175px;
    margin-bottom: 25px;
  }
  
  .vinyl-ad-card {
    background-color: #221a20;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 80%;
    max-width: 500px;
    height: 150px; /* Set a fixed height for the card */
    margin: 10px auto;
    padding: 0;
    border: 2px solid rgb(245,195,40);
    transition: transform 0.3s ease;
    margin-top: 0;
    margin-bottom: 25px;
  }
  
  
  /* vinyl ad */

  .vinyl-ad-img {
    height: 150px;
    width:150px;
    flex: 1;
    object-fit: cover; 
  }
  
  .vinyl-ad-text {
    font-family: 'Reem Kufi Fun', sans-serif;
    display: block;
    flex-direction: column;
    justify-content: center;
    flex: 2;
    padding: 30px;
    font-size: 20px;
    color: rgb(245,195,40);
  }
  
  
  /* event card */
  .event-card {
    background-color: #221a20;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    width: 80%;
    max-width: 500px;
    height: 100px; /* Set a fixed height for the card */
    margin: 15px auto;
    padding: 0;
  }
  
  .event-card-img {
    flex: 1;
    object-fit: cover; 
  }
  
  .event-card-content {
    display: block;
    flex-direction: column;
    justify-content: space-between;
    flex: 2;
    padding: 20px;
  }
  
  .event-card-title {
    font-family: 'Reem Kufi Fun', sans-serif;
    font-size: 10px;
    color: rgb(245,195,40);
    margin: 0; 
  }
  
  .event-card-price {
    font-family: 'Reem Kufi Fun', sans-serif;
    font-size: 7px;
    color: #d5d4b7;
    align-self: end;
    margin-right: 15px;
  }
  
  .event-card-date {
    font-family: 'Reem Kufi Fun', sans-serif;
    font-size: 7px;
    color: #d5d4b7;
  }
  
  .event-card-location {
    font-family: 'Reem Kufi Fun', sans-serif;
    font-size: 7px;
    color: #d5d4b7;
  }
  
  
  /* product card */
  .products-container {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;  /* start the cards from the left */
      max-width: 500px;
      width: 80%;
      margin: 50px auto; 
  }
  
  .product-card {
      width: 80px; 
      height: 135px ;
      margin: 15px;
      color: inherit;
      background-color: #221a20;
      transition: transform 0.2s;
  }
 
  
  .product-img {
      width: 80px;
      height: 80px;
      object-fit: cover;
      margin-bottom: 5px;
  }
  
  .product-name {
      font-family: 'Reem Kufi Fun', sans-serif;
      font-size: 10px;
      text-align: center;
      color: rgb(245,195,40);
      margin-top: 0px;
  }
  
  .product-price {
      font-family: 'Reem Kufi Fun', sans-serif;
      font-size: 8px;
      font-weight: bold;
      text-align: center;
      color: rgb(245,195,40);
  }
  
  #product-links {
    text-decoration: none;
  }
  
  
  /* nav bar */
  .navbar {
      background-color: #221a20;
      padding: 10px 0; /* Vertical padding, removed horizontal padding */   
      width: 100vw;
  }
  
  .navbar-content {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 80%;
      max-width: 1100px; /* or whatever maximum width you prefer */
      margin: 0 auto; /* This will center the navbar content */
      padding: 0 20px; /* Add horizontal padding here for breathing space */
  }
  
  .icon-container {
    margin-left: 20px;
  }
  
  .nav-links {
      font-family: 'Reem Kufi Fun', sans-serif;
      font-size: 15px;
      display: flex;
      margin-top: 5px;
      gap: 10px; /* This will provide spacing between each link and the button */
      margin-left: 10px;
  }
  
  .navbar img {
    margin-top: 5px;
      height: 50px;
      width: auto;
      transition: opacity 0.3s ease, transform 0.3s ease; /* smooth transitions */
  }
  
  .navbar a {
      text-decoration: none;
      color: #d5d4b7;
      transition: color 0.3s; /* Smooth color transition */
  }
  
  .shop-icon {
      background-color: #d5d4b7; /* Slightly lighter than the navbar background */
      border: none;
      border-radius: 3.5px;
      color: #000000; /* White text */
      padding: 5px 15px; /* Vertical and horizontal padding */
      cursor: pointer;
      transition: background-color 0.3s; /* Smooth background-color transition */
      margin-left: 9px;
  }
  
  
  /* product details */
  .product-details-container {
    display: flex;             
    flex-direction: column;
    align-items: center;
    margin: 40px 0px 0px 0px;
    width: 100%;
  }
  
  .product-image-container {
    margin-bottom: 20px;
  }
  
  #details-image {
    border-style: solid;
    height: 300px;
  }
  
  .product-options-container {
    font-family: 'Reem Kufi Fun', sans-serif;
    color: #d5d4b7;  
    display: flex;
    flex-direction: column;   
    align-items: center;  
    justify-content: center;
    background-color: #221a20;
    width: 300px;
    /* margin-left: 50px; */
    height: 300px;
    margin-bottom: 0;
  }
  
  .product-details-name {
    margin-top: 10px;
    margin-bottom: 0px;
    font-size: 25px;
  }
  
  .product-details-description {
    margin-bottom: 0px;
    font-size: 15px;
  }
  
  .details-separator-line {
    width: 250px;
    height: 2px; 
    background-color: #d5d4b7;
    border: 0;
    margin-bottom: 0px;
  }
  
  #size-dropdown {
    padding: 0.4rem ;               
    font-size: 10px;                    
    font-family: 'Reem Kufi Fun', sans-serif;
    border: 2px solid #d5d4b7;             
    border-radius: 8px;               
    background-color: #221a20;          
    outline: none;                     
    cursor: pointer;                  
    color: #d5d4b7;                      
    width: 80px;                      
  }
  
  #gender-dropdown {
    padding: 0.4rem ;               
    font-size: 10px;                    
    font-family: 'Reem Kufi Fun', sans-serif;
    border: 2px solid #d5d4b7;             
    border-radius: 8px;               
    background-color: #221a20;          
    outline: none;                     
    cursor: pointer;                  
    color: #d5d4b7;                      
    width: 80px; 
    margin-bottom: 15px;
  }
  
  
  .product-details-price {
    font-size: 20px;
    margin-bottom: 10px;
  }
  
  .quantity-control {
    display: flex;
    align-items: center;
  }
  
  .details-quantity-button {
    background-color: rgb(245,195,40);
    color: #000000;
    width: 20px; 
    text-align: center;
    font-size: 5px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    padding: 8px 0px; 
  }
  
  .quantity-display {
    color: #d5d4b7;
    margin: 15px 15px;
    font-size: 15px;        
  }
  
  .add-to-cart-btn {
    padding: 8px 15px;  
    background-color: rgb(245,195,40);  
    color: #000000;            
    margin-top: 10px; 
    margin-bottom: 20px;       
    cursor: pointer;         
    border: none;          
    border-radius: 8px;  
    transition: transform 0.3s; /* for a hover effect below */  
    font-size: 10px;
  }
  
  
    
