    }
  
    .payment-complete-home {
      font-family: 'Reem Kufi Fun', sans-serif;
      font-size: x-large;
  
    }
  
    .complete-container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 50vh; 
    }
  
    #payment-message {
      font-family: 'Reem Kufi Fun', sans-serif;
      font-size: x-large;
      color: rgb(245,195,40);
  
    }
  
