     /* photos */
     .photos-container {
      display: flex;
      flex-wrap: wrap; /* allows images to wrap onto the next line if there's no room */
      justify-content: center; /* centers images horizontally */
      padding: 20px; /* some padding around the container for breathing space */
  }
  
  .photos-container img {
    height: 500px;
    width: auto;
      margin: 30px; /* spacing around each image */
      box-shadow: 0 4px 8px rgba(250, 245, 245, 0.521); /* a subtle shadow for a bit of depth */
      transition: transform 0.3s; /* for a hover effect below */
  }
  
  .photos-container img:hover {
      transform: scale(1.05); /* a small zoom effect on hover */
  }
  
  #address-form {
    flex-direction: column;
    width: 30vw;
    min-width: 500px;
    align-self: center;
    box-shadow: 0px 0px 0px 0.5px rgba(252, 252, 255, 0.1),
        0px 2px 5px 0px rgba(50, 50, 93, 0.1), 0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
    border-radius: 7px;
    margin-top: 20px;
    padding: 40px;
  }
  
  /* Adjustments for Shipping Details */
  .shipping-details {
    justify-content: center;
    display: flex;
  }
  
  /* Styling for the heading */
  .shipping-details h3 {
    font-family: 'Reem Kufi Fun', sans-serif;
    color: #5469d4;
  }
  
  
  
  
  
  /* admin form */
  .form-label {
    color: #ccc;
  }
  
  
  
  /* media */
  .media-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 equal columns */
    gap: 40px; /* increased space between grid items */
    max-width: 1200px;
    margin: auto;
    margin-top: 100px;
  }
  
  .media-container img {
    width: 100%; /* image width equal to column width */
    object-fit: cover; /* images cover the area without stretching */
    aspect-ratio: 1 / 1; /* images remain square */
    transition: transform 0.3s ease;
    border: 2px solid #d5d4b7;
    
  }
  
  .media-container img:hover {
    transform: scale(1.1); /* Slightly increase the size on hover */
    
  }
  
  /* Address Form Container */
  #address-form {
    flex-direction: column;
    width: 30vw;
    min-width: 500px;
    align-self: center;
    box-shadow: 0px 0px 0px 0.5px rgba(252, 252, 255, 0.1),
                0px 2px 5px 0px rgba(50, 50, 93, 0.1),
                0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);
    border-radius: 7px;
    padding: 40px;
  }
  
  /* Input Fields Styling */
  #address-form input[type="text"] {
    padding: 10px;
    margin-bottom: 20px;
    border-radius: 4px;
    font-weight: 200;
    font-size: 16px;
    width: 300px; 
    color: rgb(227,227,227); 
    background-color: rgb(48,49,61); 
    border: 0.5px solid rgb(227,227,227, 0.1); 
  }
  
  #address-form input:focus {
    border: 0.1px solid rgba(176, 176, 176, 0.5);
    outline: none;
    box-shadow: 0px 0px 1px 3px rgba(25, 107, 18, 0.2),
                0px 0px 1px 3px rgba(22, 102, 15, 0.2),
                0px 0px 1px 3px rgba(27, 108, 20, 0.2);
  }
  
  #address-form button[type="submit"] {
    background: #5469d4;
    color: #ffffff;
    border-radius: 4px;
    border: 0;
    padding: 12px 16px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    display: block;
    transition: all 0.2s ease;
    box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);
    width: 100%;
    margin-top: 0;
  }
  
  #address-form button[type="submit"]:hover {
    filter: contrast(115%);
  }
  
  #address-form button[type="submit"]:disabled {
    opacity: 0.5;
    cursor: default;
  }
  
  .address-form-title {
    color: rgb(227,227,227);
    font-family: Arial, sans-serif;
    margin-top: 0;
    margin-bottom: 0;
    font-size: 16px;
      font-weight: 300;
      margin-bottom: 4px;
  }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  

  
  /* product in cart */
  
  
  
  .cart-product-card {
    position: relative;
    background-color: #221a20;
    display: flex;
    flex-direction: row;
    justify-content: space-between; 
    align-items: center; /* This will vertically center the content */
    width: 80%;
    max-width: 1100px;
    height: 200px;
    margin: 30px auto;
    padding: 0;
  }
  
  
  .cart-product-image {
    flex: 0 0 auto; /* Do not grow or shrink */
    object-fit: cover;
    width: 200px; /* Fixed square dimension */
    height: 200px; /* Fixed square dimension */
